@import url(https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap);
body {
  background: url(/tienda-gaijin-canibano/static/media/bg.c0b6117b.jpg) no-repeat center center fixed;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  margin-top: 10vw;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.off{
  color: blue;
}

.background, .tableDark, .tableDetail {
  border: white solid thick;
  background-color: rgba(0, 0, 0, 0.6);
  font-family: "Poppins";
  margin: 2%;
  padding: 10px;
  color: white;
  width: auto; 
  border-radius: 10px;
}

.container{
  display: flex;
  flex-flow: column; 
  align-items: center;
}

img{
  border-radius: 10px;
}

button:hover{
  -webkit-filter:brightness(120%);
          filter:brightness(120%)
}

button:active{
  -webkit-filter:brightness(70%);
          filter:brightness(70%);
}

.NavBar {
    display: flex;
    justify-content: space-around;
    font-size: 2vw;
    font-family: "Poppins";
    position: fixed;
    width: 96%;
    top:2px;
    left: 1.5%;
    border: black solid thick;
    background: grey;
    border-radius: 10px;
    z-index: 10;
}

.NavLink, .off, a, #Cart{
    color: black;
    flex-grow: 1;
    cursor: pointer;
    flex-shrink: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.NavLink:hover, #Cart:hover{
    color: white;
    background-color: black;
}
.off{
    color: darkblue;
}
.off:hover{
    color: lightblue;
}

h1 {
    color: white;
    font-size: 3vw;
    font-family: "Permanent Marker";
}

b{
    color: rgb(188,0,45);
    font-size: 1.5vw;
}


#Cart{
    background-color: lightblue;
    border-radius: 0 5px 5px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4vw;
}

.links:hover, #Cart:hover{
    color: white;
    background-color: black;
}
#ItemListContainer{
    display: flex;
    justify-content: space-evenly;
}
img{
    width: 10%;
}

.tableDark {
    border-collapse: collapse;
    color: white;
    display: flex;
    flex-wrap: wrap;
    border-radius: 10px;
}

.subTable{
    display: flex;
    border: 2px solid #a0a0a0; 
    min-width: 18%;
    flex-flow: column;
    padding: 1.3%;
    justify-content: space-between;
    margin: 1%;
    border-radius: 10px;
    flex-grow: 1;
    max-width: 28%;
}

.tableDark div img {
    width: 100%; 
}

b {
    color: #bc002d;
    font-family: "Permanent Marker";
    font-size:  1rem;
}

p {
    font-family: "Poppins";
    font-size: 1rem;
}

button{
    width: 10px;
}
.tableDetail{
    display: flex;
    max-width: 100%;
    border: white thick solid;
    flex-flow: row;
}

img{
    width: 100%;
}

.tableDark div img {
    width: 100%; 
}

b {
    color: #bc002d;
    font-family: "Permanent Marker";
    font-size:  1rem;
}

p {
    font-family: "Poppins";
    font-size: 1rem;
}

button{
    width: 10px;
}

.ReactModal__Overlay--after-open{
    background:none !important;
}

.ReactModal__Content{
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    margin-top: 10%;
    background-color: rgb(255, 255, 255, 0.5) !important;
    border-radius: 10px;
}
